import React, { Component } from 'react';

import './order_history.scss';
import axios from 'axios';

import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

class order_history extends Component {
	constructor(props) {
		super(props);
		this.state = {
			orders: [],
			startDate: new Date(),
			states: [],
			cities: []
		};
	}

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/orders/all_orders/')
			.then((response) => {
				let orders = response.data.order;
				axios
					.get('http://stage.medicento.com:8080/api/area/state/')
					.then((response) => {
						console.log(response);
						this.setState({ states: response.data, orders: orders });
					})
					.catch((err) => {
						console.log(err);
						this.setState({ orders: response.data.orders });
					});
			})
			.catch((err) => {});
	}

	change_city_dropdown = (data) => {
		axios
			.get('http://stage.medicento.com:8080/api/area/city/?state=' + data.target.value)
			.then((response) => {
				console.log(response);
				this.setState({ cities: response.data });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	change_area_dropdown = (data) => {
		axios
			.get('http://stage.medicento.com:8080/api/area/area/?city=' + data.target.value)
			.then((response) => {
				console.log(response);
				this.setState({ areas: response.data });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	handleChange = (date) => {
		this.setState({
			startDate: date
		});
	};

	render() {
		const orders = this.state.orders;

		return (
			<div class="container order_history">
				<h3 class="d-flex justify-content-center">All Orders</h3>
				Start Date : <DatePicker selected={this.state.startDate} onChange={this.handleChange} />
				&nbsp; End Date : <DatePicker selected={this.state.startDate} onChange={this.handleChange} />
				<button className="btn btn-primary" style={{ marginLeft: '10px' }}>
					Filter
				</button>
				<br />
				<div className="row m-0">
					<div class="state mr-3">
						<select name="state" id="state" onChange={this.change_city_dropdown}>
							<option value="" selected disabled>
								Select State
							</option>
							{this.state.states.map((state) => <option value={state.id}>{state.name}</option>)}
						</select>
					</div>
					<div class="city mr-3 ">
						<select name="city" id="city" onChange={this.change_area_dropdown}>
							<option value="" selected disabled>
								Select City
							</option>
							{this.state.cities.map((city) => <option value={city.id}>{city.name}</option>)}
						</select>
					</div>
				</div>
				<br />
				<br />
				<table class="tableData" class="table table-striped table-bordered">
					<thead>
						<th>Order Code</th>
						<th>Retailer</th>
						<th>Amount</th>
						<th>Date</th>
						<th>State</th>
						<th>City</th>
					</thead>
					<tbody>
						{orders.map((order) => (
							<tr>
								<td>{order.id}</td>
								<td>{order.pharmacy.name}</td>
								<td>{order.grand_total}</td>
								<td>{order.created_at}</td>
								<td>{order.pharmacy.state.name}</td>
								<td>{order.pharmacy.city.name}</td>
							</tr>
						))}
					</tbody>
				</table>
			</div>
		);
	}
}
export default order_history;
