import React, { Component } from 'react';

import './login.scss';

import { withRouter } from 'react-router-dom';

import axios from 'axios';

class login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: '',
			password: '',
			isLoading: false,
			invalid: false
		};
		this.changeLogin = props.changeLogin;
	}

	setEamilValue = (event) => {
		let username = event.target.value;
		this.setState({ username: username });
	};

	setPasswordValue = (event) => {
		let password = event.target.value;
		this.setState({ password: password });
	};

	sendLoginRequest = (event) => {
		event.preventDefault();
		this.setState({ isLoading: true });
		const formData = new FormData();
		formData.append('username', this.state.username);
		formData.append('password', this.state.password);
		console.log(formData);
		axios
			.post('http://stage.medicento.com:8080/api/app/admin_login/', formData)
			.then((response) => {
				console.log(response);
				if (response.data.message == 'User Logged In') {
					this.changeLogin(true);
					this.setState({ isLoading: false, invalid: false });
					this.props.history.push('/');
				} else {
					this.setState({ invalid: true, isLoading: false });
				}
			})
			.catch((err) => {
				this.setState({ isLoading: false });
			});
	};

	render() {
		return (
			<div className="login">
				<div className="container">
					<form className="login_form" onSubmit={this.sendLoginRequest}>
						{this.state.invalid ? (
							<div class="alert alert-danger" role="alert">
								Invalid Details
								<button type="button" class="close" data-dismiss="alert" aria-label="Close">
									<span aria-hidden="true">&times;</span>
								</button>
							</div>
						) : null}
						<div className="form-group">
							<label htmlFor="exampleInputEmail">Username</label>
							<input
								type="username"
								className="form-control"
								onChange={this.setEamilValue}
								id="exampleInputEmail"
								placeholder="Enter username"
								required
							/>
						</div>
						<div className="form-group">
							<label htmlFor="exampleInputPassword">Password</label>
							<input
								type="password"
								className="form-control"
								id="exampleInputPassword"
								onChange={this.setPasswordValue}
								placeholder="Password"
								required
							/>
						</div>
						{this.state.isLoading ? (
							<div className="loader">
								<img
									src="https://wpamelia.com/wp-content/uploads/2018/11/ezgif-2-6d0b072c3d3f.gif"
									height="100"
									width="250"
									alt=""
								/>
							</div>
						) : null}
						<div className="form-group-submit">
							<input type="submit" value="Login" className="btn btn-primary" />
						</div>
					</form>
				</div>
			</div>
		);
	}
}

export default withRouter(login);
