import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './sidenav.scss';
class sidenav extends Component {
	constructor(props) {
		super(props);
		this.state = {
			active_menu_item: 0
		};
	}

	changeMenuLink = (i) => {
		this.setState({ active_menu_item: i });
		this.props.hideSideNav();
	};

	render() {
		const active_menu_item = this.state.active_menu_item;

		return (
			<div className="sidebar">
				<ul>
					<Link to="/">
						<li className={active_menu_item === 0 ? 'active' : null} onClick={() => this.changeMenuLink(0)}>
							<i class="fas fa-home" /> Home
						</li>
					</Link>
					<li>
						<div className="members" data-toggle="collapse" href="#collapseOrder">
							<i class="fa fa-user-plus" /> Orders <i class="fa fa-angle-down rotate-icon" />
						</div>
						<ul class="list-unstyled" className="collapse" id="collapseOrder">
							<Link to="/order_stats">
								<li
									className={active_menu_item === 1 ? 'active' : null}
									onClick={() => this.changeMenuLink(1)}
								>
									<i class="fas fa-bars    " /> Order Stats
								</li>
							</Link>
							<Link to="/order_history">
								<li
									className={active_menu_item === 2 ? 'active' : null}
									onClick={() => this.changeMenuLink(2)}
								>
									<i class="fas fa-history    " /> Order History
								</li>
							</Link>
						</ul>
					</li>
					<Link to="/inventory_selection">
						<li className={active_menu_item === 3 ? 'active' : null} onClick={() => this.changeMenuLink(3)}>
							{' '}
							<i class="fas fa-bus    " /> Distributor Selection
						</li>
					</Link>
					<Link to="/marketing">
						<li className={active_menu_item === 4 ? 'active' : null} onClick={() => this.changeMenuLink(4)}>
							<i class="fa fa-envelope" /> Marketing
						</li>
					</Link>
					<li>
						<div className="members" data-toggle="collapse" href="#collapseArea">
							<i class="fa fa-user-plus" /> Area <i class="fa fa-angle-down rotate-icon" />
						</div>
						<ul class="list-unstyled" className="collapse" id="collapseArea">
							<Link to="/area">
								<li
									className={active_menu_item === 5 ? 'active' : null}
									onClick={() => this.changeMenuLink(5)}
								>
									<i class="fas fa-globe    " /> Area Dashboard
								</li>
							</Link>
							<Link to="/area_stats">
								<li
									className={active_menu_item === 6 ? 'active' : null}
									onClick={() => this.changeMenuLink(6)}
								>
									<i class="fas fa-globe" /> Area Statistics
								</li>
							</Link>
						</ul>
					</li>
					<li>
						<div className="members" data-toggle="collapse" href="#collapsePharma">
							<i class="fa fa-user-plus" /> Pharmacy <i class="fa fa-angle-down rotate-icon" />
						</div>
						<ul class="list-unstyled" className="collapse" id="collapsePharma">
							<Link to="/pharmacy_dashboard">
								<li
									className={active_menu_item === 7 ? 'active' : null}
									onClick={() => this.changeMenuLink(7)}
								>
									<i class="fas fa-globe    " /> Pharmacy DashBoard
								</li>
							</Link>
							<Link to="/cash_collection">
								<li
									className={active_menu_item === 8 ? 'active' : null}
									onClick={() => this.changeMenuLink(8)}
								>
									<i class="fas fa-globe    " /> Cash Collection
								</li>
							</Link>
						</ul>
					</li>
					<li>
						<div className="members" data-toggle="collapse" href="#collapseExample">
							<i class="fa fa-user-plus" /> Add Members <i class="fa fa-angle-down rotate-icon" />
						</div>
						<ul class="list-unstyled" className="collapse" id="collapseExample">
							<li>Add Retailer</li>
							<li>Add Distributor</li>
							<li>Add Sales Person</li>
						</ul>
					</li>
				</ul>
			</div>
		);
	}
}

export default sidenav;
