import React, { Component } from 'react';
import './App.css';
import { BrowserRouter, Route } from 'react-router-dom';

import InventorySelection from './components/inventory_selection/inventory_selection';
import ChangeDistributor from './components/change_distributor/change_distributor';
import InventoryUploading from './components/inventory_uploading/inventory_uploading';
import OrderStats from './components/order_stats/order_stats';
import SideNav from './components/sidenav/sidenav';
import Marketing from './components/marketing/marketing';
import OrderHistory from './components/order_history/order_history';
import Area from './components/area/area';
import CashCollection from './components/cash_collection/cash_collection';

import Navbar from './components/navbar/navbar';

import Main from './components/main/main';

import Login from './components/login/login';

import Area_Dhasboard from './components/area_dashboard/area_dashboard';
import Pharmacy_Dashboard from './components/pharmacy_dashboard/pharmacy_dashboard';

import { useSelector } from 'react-redux';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = {
			is_logged_in: false,
			hideSideNavBar: false
		};
	}

	changeLogin(is_logged_in) {
		this.setState({ is_logged_in: is_logged_in });
	}

	hideSideNav() {
		let hideSideNavBar = this.state.hideSideNavBar;
		this.setState({ hideSideNavBar: !hideSideNavBar });
	}

	render() {
		const is_logged_in = this.state.is_logged_in;
		const hideSideNavBar = this.state.hideSideNavBar;

		return (
			<BrowserRouter>
				<div className="App">
					<Navbar
						is_logged_in={this.state.is_logged_in}
						changeLogin={this.changeLogin.bind(this)}
						hideSideNav={this.hideSideNav.bind(this)}
					/>
					<div className="container_main">
						{is_logged_in && hideSideNavBar ? (
							<div className="left_wrapper">
								<SideNav hideSideNav={this.hideSideNav.bind(this)} />
							</div>
						) : null}
						{is_logged_in ? (
							<div className="right_wrapper">
								<Route exact path="/" component={Main} />
								<Route path="/inventory_mapping/:id" component={InventorySelection} />
								<Route path="/inventory_selection" component={ChangeDistributor} />
								<Route path="/marketing" component={Marketing} />
								<Route path="/order_history" component={OrderHistory} />
								<Route path="/area" component={Area} />
								<Route path="/cash_collection" component={CashCollection} />
								<Route path="/area_stats" component={Area_Dhasboard} />
								<Route path="/pharmacy_dashboard" component={Pharmacy_Dashboard} />
								<Route path="/order_stats" component={OrderStats} />
							</div>
						) : (
							<div className="right_wrapper">
								<Route path="/" render={() => <Login changeLogin={this.changeLogin.bind(this)} />} />
							</div>
						)}
					</div>
				</div>
			</BrowserRouter>
		);
	}
}

export default App;
