import React, { Component } from 'react';

import './pharmacy_dashboard.scss';
import axios from 'axios';

export default class pharmacy_dashboard extends Component {
	state = {
		pharmas: [],
		total_city: [],
		total_state: [],
		active_city: [],
		active_state: [],
		active_pharmacy: [],
		temp_pharma: []
	};

	componentWillMount() {
		let cities = [];
		let states = [];
		let active_cities = [];
		let active_states = [];
		let pharmacies = [];
		axios
			.get('http://stage.medicento.com:8080/pharmacy/pharmacy/')
			.then((response) => {
				console.log(response);

				for (var i = 0; i < response.data.length; i++) {
					var isPresent = false;
					for (var j = 0; j < cities.length; j++) {
						if (cities[j].name == response.data[i].city.name) {
							isPresent = true;
							break;
						}
					}
					var isPresentState = false;

					for (var j = 0; j < states.length; j++) {
						if (states[j].name == response.data[i].state.name) {
							isPresentState = true;
							break;
						}
					}
					if (!isPresentState) {
						states.push(response.data[i].state);
					}

					if (!isPresent) {
						cities.push(response.data[i].city);
					}

					if (response.data[i].order_count > 0) {
						pharmacies.push(response.data[i]);
						var isPresent = false;
						for (var j = 0; j < active_cities.length; j++) {
							if (active_cities[j].name == response.data[i].city.name) {
								isPresent = true;
								break;
							}
						}
						var isPresentState = false;

						for (var j = 0; j < active_states.length; j++) {
							if (active_states[j].name == response.data[i].state.name) {
								isPresentState = true;
								break;
							}
						}
						if (!isPresentState) {
							active_states.push(response.data[i].state);
						}

						if (!isPresent) {
							active_cities.push(response.data[i].city);
						}
					}
				}
				console.log(cities);
				this.setState({
					pharmas: response.data,
					total_city: cities,
					total_state: states,
					active_city: active_cities,
					active_state: active_states,
					active_pharmacy: pharmacies,
					temp_pharma: response.data
				});
			})
			.catch((err) => {
				console.log(err);
			});
	}

	activePharmacy(status) {
		if (status == 1) {
			this.setState({
				temp_pharma: this.state.active_pharmacy
			});
		} else {
			this.setState({
				temp_pharma: this.state.pharmas
			});
		}
	}

	render() {
		const temp_pharma = this.state.temp_pharma;

		return (
			<div className="pharmacy_dashboard">
				<h2 class="bg-primary text-center text-white p-3">Medicento Pharmacy List</h2>
				<section class="container">
					<div class="row" style={{ color: 'white' }}>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div
								class="card bg-primary"
								style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
								onClick={() => this.activePharmacy(0)}
							>
								<div class="card-body">
									<div class="card-title">Total Pharmacy</div>
									<div class="card-text">
										<span class="fa fa-angle-double-right" />
										{' ' + this.state.pharmas.length}
									</div>
								</div>
							</div>
						</div>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div class="card bg-info" style={{ borderLeft: '5px solid black', cursor: 'pointer' }}>
								<div class="card-body">
									<div class="card-title">Total States</div>
									<div class="card-text">
										<span class="fa fa-angle-double-up" />
										{' ' + this.state.total_state.length}
									</div>
								</div>
							</div>
						</div>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div class="card bg-danger" style={{ borderLeft: '5px solid black', cursor: 'pointer' }}>
								<div class="card-body">
									<div class="card-title">Total City</div>
									<div class="card-text">
										<span class="fa fa-angle-double-up" />
										{' ' + this.state.total_city.length}
									</div>
								</div>
							</div>
						</div>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div
								class="card bg-info"
								style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
								onClick={() => this.activePharmacy(1)}
							>
								<div class="card-body">
									<div class="card-title">Active Pharmacy</div>
									<div class="card-text">
										<span class="fa fa-angle-double-up" />
										{' ' + this.state.active_pharmacy.length}
									</div>
								</div>
							</div>
						</div>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div class="card bg-info" style={{ borderLeft: '5px solid black', cursor: 'pointer' }}>
								<div class="card-body">
									<div class="card-title">Active States</div>
									<div class="card-text">
										<span class="fa fa-angle-double-up" />
										{' ' + this.state.active_state.length}
									</div>
								</div>
							</div>
						</div>
						<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
							<div class="card bg-danger" style={{ borderLeft: '5px solid black', cursor: 'pointer' }}>
								<div class="card-body">
									<div class="card-title">Active City</div>
									<div class="card-text">
										<span class="fa fa-angle-double-up" />
										{' ' + this.state.active_city.length}
									</div>
								</div>
							</div>
						</div>
					</div>

					<table className="table table-bordered">
						<thead>
							<tr>
								<th>Pharmacy Name</th>
								<th>Pharmacy Address</th>
								<th>Pharmacy Code</th>
								<th>City</th>
								<th>State</th>
								<th>Phone</th>
								<th>Edit</th>
								<th>View Details</th>
							</tr>
						</thead>
						<tbody>
							{temp_pharma.map((pharma) => (
								<tr>
									<td>{pharma.name}</td>
									<td>{pharma.address} </td>
									<td>{pharma.pharma_code}</td>
									<td>{pharma.city.name}</td>
									<td>{pharma.state.name}</td>
									<td>{pharma.mobile_no}</td>
									<td>
										<span class="fa fa-pencil-alt" />
									</td>
									<td>
										<span class="fa fa-address-card" />
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</section>
			</div>
		);
	}
}
