import React, { Component } from 'react';

import './main.scss';
import axios from 'axios';
import { stat } from 'fs';

class main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			total_orders: 0,
			orders: [],
			active_orders: 0,
			cancel_orders: 0,
			delivered_orders: 0,
			un_delivered_orders: 6,
			returned_orders: 0,
			pageNo: 1,
			noMoreData: false,
			status: 'Placed'
		};
	}

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/orders/get_order_count/')
			.then((response) => {
				console.log(response);
				this.setState({
					total_orders: response.data.total_orders,
					active_orders: response.data.active_orders,
					cancel_orders: response.data.cancel_orders,
					delivered_orders: response.data.delivered_orders,
					returned_orders: response.data.returned_orders,
					un_delivered_orders: response.data.un_delivered_orders,
					orders: response.data.placed_orders,
					pageNo: 2,
					noMoreData: false
				});
			})
			.catch((err) => {});
	}

	changeOrderTab(status) {
		axios
			.get('http://stage.medicento.com:8080/orders/get_orders_with_status/?status=' + status + '&pagno=1')
			.then((response) => {
				console.log(response);
				if (response.data.order.length > 0) {
					this.setState({ orders: response.data.order, pageNo: 2, status: status, noMoreData: false });
				} else {
					this.setState({ noMoreData: true, pageNo: this.state.pageNo + 1, status: status });
				}
			})
			.catch((err) => {});
	}

	goToNextPage() {
		axios
			.get(
				'http://stage.medicento.com:8080/orders/get_orders_with_status/?status=' +
					this.state.status +
					'&pagno=' +
					this.state.pageNo
			)
			.then((response) => {
				if (response.data.order.length > 0) {
					this.setState({ orders: response.data.order, pageNo: this.state.pageNo + 1, noMoreData: false });
				} else {
					this.setState({ noMoreData: true, pageNo: this.state.pageNo + 1 });
				}
			})
			.catch((err) => {});
	}

	changeOrderStatus = (id, event) => {};

	render() {
		const total_orders = this.state.total_orders;
		const active_orders = this.state.active_orders;
		const cancel_orders = this.state.cancel_orders;
		const delivered_orders = this.state.delivered_orders;
		const un_delivered_orders = this.state.un_delivered_orders;
		const returned_orders = this.state.returned_orders;
		const orders = this.state.orders;

		return (
			<div className="main">
				<div class="row">
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div class="card bg-primary" style={{ borderLeft: '5px solid black' }}>
							<div class="card-body">
								<div class="card-title">Orders</div>
								<div class="card-text">
									<span class="fa fa-angle-double-right" />
									{' ' + total_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-info"
							style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
							onClick={() => this.changeOrderTab('Placed')}
						>
							<div class="card-body">
								<div class="card-title">Orders Active</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + active_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-dark"
							style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
							onClick={() => this.changeOrderTab('Cancelled')}
						>
							<div class="card-body">
								<div class="card-title">Orders Canceled</div>
								<div class="card-text">
									<span class="fa fa-angle-double-down" />
									{' ' + cancel_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-danger"
							style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
							onClick={() => this.changeOrderTab('Returned')}
						>
							<div class="card-body">
								<div class="card-title">Orders Returns</div>
								<div class="card-text">
									<span class="fa fa-angle-double-left" />
									{' ' + returned_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-success"
							style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
							onClick={() => this.changeOrderTab('Delivered')}
						>
							<div class="card-body">
								<div class="card-title">Orders Delivered</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + delivered_orders}
								</div>
							</div>
						</div>
					</div>
					<div class="col-auto col-lg-3 col-md-4 col-sm-6 mb-2">
						<div
							class="card bg-secondary"
							style={{ borderLeft: '5px solid black', cursor: 'pointer' }}
							onClick={() => this.changeOrderTab('UnDelivered')}
						>
							<div class="card-body">
								<div class="card-title">Orders Un Delivered</div>
								<div class="card-text">
									<span class="fa fa-angle-double-up" />
									{' ' + un_delivered_orders}
								</div>
							</div>
						</div>
					</div>
					<h3 className="col-md-12">{this.state.status} Orders</h3>
					<button
						className="btn btn-primary"
						style={{ marginLeft: '3vw' }}
						onClick={() => this.goToNextPage()}
					>
						Next
					</button>{' '}
					<span style={{ padding: '10px' }}> Page No {this.state.pageNo} </span>
					<table class="tableData" className="table table-striped table-bordered">
						<thead>
							<th>Order Code</th>
							<th>Retailer</th>
							<th>Amount</th>
							<th>Date</th>
							<th>Details</th>
							<th>State</th>
							<th>ChangeStatus</th>
						</thead>
						{this.state.noMoreData ? (
							<tr>
								<td colSpan="6">No More Data</td>
							</tr>
						) : (
							<tbody>
								{orders.map((order, i) => (
									<tr>
										<td>{order.id}</td>
										<td>{order.pharmacy.name}</td>
										<td>{order.grand_total}</td>
										<td>{order.created_at}</td>
										<td>
											{order.order_items.map((order_item) => (
												<div style={{ fontSize: '10px' }}>
													{order_item.medicine_name}{' '}
													<span class="badge badge-light">{order_item.quantity}</span>
												</div>
											))}
										</td>
										<td>Karnataka</td>
										<td>
											<select onChange={(e) => this.changeOrderStatus(i, e)}>
												<option value="Active" selected={order.status.name == 'Placed'}>
													Active
												</option>
												<option value="Canceled" selected={order.status.name == 'Cancelled'}>
													Cancelled
												</option>
												<option value="Delivered" selected={order.status.name == 'Delivered'}>
													Delivered
												</option>
												<option value="Returns" selected={order.status.name == 'Returned'}>
													Returns
												</option>
												<option
													value="Not Delivered"
													selected={order.status.name == 'UnDelivered'}
												>
													Not Delivered
												</option>
											</select>
										</td>
									</tr>
								))}
							</tbody>
						)}
					</table>
				</div>
			</div>
		);
	}
}

export default main;
