import React, { Component } from 'react';

import './area.scss';

import axios from 'axios';

class area extends Component {
	constructor(props) {
		super(props);
		this.state = {
			states: [],
			cities: [],
			areas: []
		};
	}

	componentWillMount() {
		axios
			.get('http://stage.medicento.com:8080/api/area/state/')
			.then((response) => {
				console.log(response);
				this.setState({ states: response.data });
			})
			.catch((err) => {
				console.log(err);
			});
	}

	change_city_dropdown = (data) => {
		axios
			.get('http://stage.medicento.com:8080/api/area/city/?state=' + data.target.value)
			.then((response) => {
				console.log(response);
				this.setState({ cities: response.data });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	change_area_dropdown = (data) => {
		axios
			.get('http://stage.medicento.com:8080/api/area/area/?city=' + data.target.value)
			.then((response) => {
				console.log(response);
				this.setState({ areas: response.data });
			})
			.catch((err) => {
				console.log(err);
			});
	};

	render() {
		const areas = this.state.areas;

		return (
			<div className="area">
				<h2 class="bg-primary text-center text-white p-3">Medicento Areas List</h2>
				<section class="container">
					<div class="search_area row my-3 mx-0">
						<div class="state mr-3">
							<select name="state" id="state" onChange={this.change_city_dropdown}>
								<option value="" selected disabled>
									Select State
								</option>
								{this.state.states.map((state) => <option value={state.id}>{state.name}</option>)}
							</select>
						</div>
						<div class="city mr-3 ">
							<select name="city" id="city" onChange={this.change_area_dropdown}>
								<option value="" selected disabled>
									Select City
								</option>
								{this.state.cities.map((city) => <option value={city.id}>{city.name}</option>)}
							</select>
						</div>
						<div class="input-group mt-2">
							<input
								type="text"
								name="area_name"
								id="area_name"
								placeholder="Enter Area"
								class="form-control"
							/>
							<input
								type="text"
								name="area_pincode"
								id="area_pincode"
								placeholder="Enter Pincode"
								class="form-control"
							/>
							<button class="btn btn-success input-group-append" onclick="addNewArea()">
								Add New Area
							</button>
						</div>
					</div>
					<table className="table table-bordered">
						<thead>
							<tr>
								<th>Id</th>
								<th>Area</th>
								<th>Pincode</th>
								<th>Edit</th>
							</tr>
						</thead>
						<tbody>
							{areas.map((area) => (
								<tr>
									<td>{area.id}</td>
									<td>{area.name}</td>
									<td>{area.pincode}</td>
									<td>
										<button className="btn btn-primary">Edit</button>
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</section>
			</div>
		);
	}
}

export default area;
